// @flow

import React from 'react';
import Button from '@material-ui/core/Button';

const api = (url: string) =>
  fetch(url, {
    method: 'GET',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json'
    }
  }).then(response => {
    if (response.ok) {
      return response.json();
    }

    throw new Error(`${response.statusText} ${response.url}`);
  });

class Caller extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      normalResponse: 'empty',
      firstResponse: 'empty',
      secondResponse: 'empty'
    };
  }

  normalClick = () => {
    api('https://playground-be1.azurewebsites.net/greeting').then(response => {
      this.setState({ normalResponse: response });
    });
  };

  firstClick = () => {
    api('https://playground-be1.azurewebsites.net/greetingFromThree').then(
      response => {
        this.setState({ firstResponse: response });
      }
    );
  };

  secondClick = () => {
    api('https://playground-be2.azurewebsites.net/greetingFromThree').then(
      response => {
        this.setState({ secondResponse: response });
      }
    );
  };

  render() {
    const { normalResponse, firstResponse, secondResponse } = this.state;

    return (
      <div>
        <div>
          <h1>Normal</h1>
          <Button onClick={this.normalClick}>Normal</Button>
          <p>{`Result from first is => ${JSON.stringify(normalResponse)}`}</p>
        </div>

        <div>
          <h1>First</h1>
          <Button onClick={this.firstClick}>First</Button>
          <p>{`Result from first is => ${JSON.stringify(firstResponse)}`}</p>
        </div>

        <div>
          <h1>Second</h1>
          <Button onClick={this.secondClick}>Second</Button>
          <p>{`Result from first is => ${JSON.stringify(secondResponse)}`}</p>
        </div>
      </div>
    );
  }
}

const RouteLanding = () => (
  <div className="route-landing">
    <Caller />
  </div>
);

export default RouteLanding;



